import React from 'react';
import Button from 'react-bootstrap/Button';
import './App.css';
import {observer} from "mobx-react";
import Switch from "react-router-dom/es/Switch";
import Route from "react-router-dom/es/Route";
import {withRouter} from "react-router";
import Form from "react-bootstrap/Form";
import Tab from "react-bootstrap/Tab";
import VehicleStore from "./stores/VehicleStore";

const vehicleStore = new VehicleStore();

function NewVehicle() {
    return (
        <Form>
            <Form.Group controlId="exampleForm.ControlInput1">
                <Form.Label>Email address</Form.Label>
                <Form.Control type="email" placeholder="name@example.com"/>
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlSelect1">
                <Form.Label>Example select</Form.Label>
                <Form.Control as="select">
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                    <option>4</option>
                    <option>5</option>
                </Form.Control>
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlSelect2">
                <Form.Label>Example multiple select</Form.Label>
                <Form.Control as="select" multiple>
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                    <option>4</option>
                    <option>5</option>
                </Form.Control>
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlTextarea1">
                <Form.Label>Example textarea</Form.Label>
                <Form.Control as="textarea" rows="3"/>
            </Form.Group>
        </Form>
    )
}

function NofossLogo() {
    return (
        <div>
            <img src="https://i.imgur.com/bVMYcYR.png" className="Logo" alt="NoFoss Logo"/>
            <Button onClick={() => vehicleStore.giraffes.push("Elmer")}>Tilf√∏j bil</Button>
        </div>
    )
}

const Tabs = withRouter(({history, match}) => {
    console.log(history);
    console.log(match);
    return(
        <Tabs defaultActiveKey="profile" id="uncontrolled-tab-example">
            <Tab eventKey="home" title="Yeet">
                <h1>Yeet</h1>
            </Tab>
            <Tab eventKey="profile" title="Yoink">
                <h1>Yoink</h1>
            </Tab>
            <Tab eventKey="contact" title="Oof" disabled>
                <h1>Oof</h1>
            </Tab>
        </Tabs>
    )
});

function App() {
    return (
        <div>
            <container>
                    <NofossLogo/>
                    <Switch>
                        <Route path={"/about/:text"} component={About}/>
                        <Route path={"/about"} component={About}/>
                        <Route path={"/newVehicle"} component={NewVehicle}/>
                        <Route exact path={"/"} render={() => <h1>NoFoss</h1>}/>
                        <Route render={() => <h1>404</h1>}/>
                    </Switch>
            </container>
            <container>
                <Tabs/>
            </container>
            <container>
                <ul>
                    {vehicleStore.giraffes.map((vehicleName, key) =>
                        <li key={key}>{vehicleName}</li>
                    )}
                </ul>
            </container>
        </div>
    );
}

const About = withRouter(({history, match}) => {
    console.log(history);
    console.log(match);
    return <div>
        <h1>About {match.params.text}</h1>
        <Button onClick={() => history.push("/")}>Go to front</Button>
    </div>
});

export default observer(App);